@import url(https://fonts.googleapis.com/css?family=Sniglet);

.video-surround{
    top:19%;
    left:10%;
    position: absolute;
    width:41.643%;
    height:66.42%;
    overflow:hidden;
    transition:all 0.8s ease-in-out;
    background-color: rgb(0,0,0);
    box-shadow: 0 0 23px rgb(0,0,0);
    border-radius:35px;
}
.cricon-surround{
    position:absolute;cursor: pointer;
    width:45px;height:45px;margin-right:2%;
    background-image:linear-gradient(45deg,rgb(220,20,60) 0,rgb(200,0,40) 50%,rgb(0,0,60) 50%,rgb(0,0,90) 100%);
    background-size: 300%;
    display:flex;justify-content: center;align-items: center;
    text-align:center;
    border-radius:50%;
    box-shadow:0 0 13px rgb(220,20,20,0.6), 0 0 8px rgb(0,0,0) inset;
    transition: ease-in-out all 0.6s;
    top:87%;
}
.cricon-surround:hover{
    background-position:99%;
    box-shadow:0 0 13px rgb(0,0,90);
}
.vid{
    left:30%
}
.aud{
    right:30%
}
.cricon{
    font-size:25px;
    color:black;
}
@import url(https://fonts.googleapis.com/css?family=Ultra);
.crbtn{
    position: absolute;
    font-family:Sniglet;
    font-size:25px;
    font-weight: 600;
    text-shadow: 0 0 4px rgb(255,255,255,0.8);
    border:none;outline:none;display:flex;align-items: center;justify-content: center;
    color:white;padding:10px;
    transition: all 0.3s, background-position 0.9s ease;
    cursor:pointer;
    width:180px;
    padding:6px;
    padding-left:10px;padding-right: 10px;
    background-image:linear-gradient(rgb(20,20,20),rgb(20,20,30));
    height:50px;
    border-radius:20px;
    top:64%;left:calc(50% - 90px);
    box-shadow:0 0 13px rgb(20,30,40);
}
.crbtn:active{
    transform:scale(0.9)
}
.crlunderline{
    position: absolute;
    top:40%;
    padding-bottom:10px;
    width:280px;
    height:45px;
    display:flex;flex-direction: row;
    align-items: center;
    transition:all 0.6s ease-in-out,top 0.3s ease-in-out;
    padding:8px;
    z-index:244;
    text-shadow: 0 0 4px rgb(255,255,255,0.8);
    background-color: black;
    box-shadow:0 0 22px rgb(0,0,0),0 0 8px rgb(0,0,0) inset;
    border-radius:35px;
}
.join-room{
    background: transparent;outline: none;border:none;font-family: inherit;
    color:white;font-size:16px;font-weight: 620;margin-left:0%;
    width:214px;
    height:100%;text-decoration: none;
    transition: all 0.4s ease-in-out;
}

.join-room:focus{
    background-position: 100%;
}
.join-room::placeholder{
    font-size:15px;font-weight: 550;
    color:rgb(255,255,255,0.8);
}
.room-name{
    position: absolute;
    top:35%;
    font-size:60px;
    font-family:Pacifico;
    display:flex;align-items:center;justify-content: center;
    border-radius:5px;
    color:rgba(255,255,255);
    text-shadow: 0 0 5px rgb(255,255,255);
    font-weight: 550;
}
.create-room-heading{
    font-size:102px;
    font-family: Pacifico;
    position: absolute;
    text-align: center;
    right:3%;
    text-shadow: 0 0 4px rgb(250,250,250);
    color:white;
}

.crl{
    position: absolute;
    top:30%;
    right: 40px;
    width:490px;
    height:350px;   
    z-index:5;
    display:flex;align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top:12%;
    border-radius: 40px;
    overflow: hidden;
    background-color:red;
    transition:ease-in-out all 0.4s;
    box-shadow:0 0 22px rgba(0,0,0) ,0 0 13px rgb(0,0,0) inset;
}
.crl .circle-temp{
    top:-200px;
    left:-350px;
}
.crl .circle-temp1{
    width:300px; height: 300px;
    top:70%;left:270px;
}
.crl .circle-temp2{
    width:400px;height: 400px;
    top:-190px;
    right:-230px;
}
.crlicon-surround{
    width:35px;height:35px;margin-right:5px;
    display:flex;justify-content: center;align-items: center;
    text-align:center;
    border-radius:50%;
    background-color:rgb(255,25,15);
    box-shadow: 0 0 8px rgba(20,20,0),0 0 5px rgb(0,0,0) inset;
    transition: ease-in-out all 0.6s;
}
.crlicon{
    font-size:16px;
    text-shadow:0 0 5px rgb(250,250,250);
    color:white;
}
.crwrong-message{
    position: absolute;
    bottom:5%;
    left:-50%;
    transform:scale(0.5);
    height: 9%;
    padding-left:15px;
    padding-right:15px;
    transition:all 0.7s ease-in-out;
    font-family: Sniglet;
    display: flex;align-items: center;justify-content:space-around;
    color:rgba(255,255,255);
    border-radius:10px;
    background-color: rgb(250,20,60);
    z-index:10;
    box-shadow: 0 0 10px rgb(0,0,0) , 0 0 5px rgb(0,0,0) inset;
}
.error-room{
    font-size:20px;
    text-shadow:0 0 5px rgb(250,250,250);
    margin-left:5px;
    margin-right: 5px;
    margin-bottom:5px;
}
.cr-mem-prelim{
    align-self: flex-start;
    margin-left:5px;
    padding:10px;
    color:rgba(255,255,255,0.7);
    text-transform: uppercase;
    font-size: 13px;font-weight: 500;
}
.cr-room{
    padding:10px;
    position:relative;
    width:95%;
    color:rgba(255,255,255,0.9);
    font-size: 15px;font-weight: 500;
    transition:transform 0.5s ease-in-out;
    cursor: pointer;
    border-radius:5px;
    font-family: Sniglet;
    display: flex;align-items: center;    /* transition:all 0.2s ease-in-out; */
}
.cr-room:hover{
    background-color:white;
    box-shadow: 0 0 22px rgba(0,0,0,0.5) inset;
    color:black;
}
.crimg-prof1{
    width:40px;
    height:40px;
    border-radius:50%;
    box-shadow:0 0 15px rgba(0,0,0,0.5) inset,0 0 22px rgb(0,0,0);
    background-color:rgb(220,20,60);
    display:flex;text-transform: uppercase;align-items: center;justify-content: center;
    font-family:Sniglet;
    font-size: 18px;font-weight: 650;
    text-shadow:0 0 12px rgb(250,250,250);
    color:rgba(255,255,255);
    margin-right:8px;
}

.available-rooms{
    position: absolute;
    top:25%;
    left:0.5%;
    width:18.5%;
    height:45%;
    border-radius:25px;
    background-color: rgb(25,50,80);
    box-shadow: 0 0 22px rgba(0,0,0,0.4) inset,0 0 16px rgb(0,0,0);
    overflow-y: scroll;
    overflow-x:hidden;
    display: flex;align-items: center;flex-direction: column;
    padding-top: 5px;
    z-index:3;
    -ms-overflow-style: none; 
    transition: all 0.8s ease-in-out;
    scrollbar-width: none;
    transform:translateX(-103%)
}
.my-rooms-btn{
    position: absolute;
    top:1.5%;
    z-index:4;
    left:0.8%;
    cursor:pointer;
    min-height: 60px;
    min-width:60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size:52px;
    font-family:Sniglet;
    color:white;
    text-shadow: 0 0 3px rgba(255,255,255,0.9);
    transition: ease-in-out all 0.8s,text-shadow 0.15s ease-in-out;
    animation:alternate infinite 3s floating1;
    background-color: rgb(175,20,20);
    box-shadow: 0 0 22px rgb(0,0,0) , 0 0 12px rgb(0,0,0) inset;
    border-radius: 38px;
}
.my-rooms-btn:hover{
    text-shadow: 0 0 7px rgba(255,255,255,0.9);
}
@keyframes floating1{
    0%{
        transform:translateY(0px);
    }
    65%{
        transform:translateY(6px);
    }
    100%{
        transform:translateY(0px);
    }
}

.my-rooms-btn .crlicon{
    font-size:33px;
}
.crwrong-message .fa-times-circle{
    font-size:20px;
    margin-right:5px;
    color:rgb(255,255,255,0.9);
    text-shadow: none;
}

.createbtn{
    top:78%;
    left:calc(108.5% - 530px);
    width:17%;
    background-image:linear-gradient(115deg,rgb(24,50,60),rgb(30,40,50));
    box-shadow:0 0 22px rgb(0,0,0), 0 0 10px rgb(0,0,0) inset;
    border-radius: 0px 0px 30px 30px;
}

.wanna-join{
    top:-30%;
    font-family: Sniglet;
    left:56%;
    width: 100px;
    height: 100px;
    border-radius:50%;
    box-shadow:0 0 12px rgb(0,0,0),0 0 12px rgb(0,0,0) inset;
    background-color:rgb(20,50,80);
    color:rgb(255,250,250);
    font-size:18px;
    transform:scale(0);
    text-shadow: 0 0 2px rgb(250,250,250);
    cursor: pointer;    
    display:flex;align-items: center;justify-content: center;
    position: absolute;
    text-align:center;
    transition:all 2s ease-in-out;
}
