@import url(https://fonts.googleapis.com/css?family=Pacifico);
@import url(https://fonts.googleapis.com/css?family=Sniglet);

html, body {
    height: 100%;
    margin: 0px;
    background-image: url("/static-img/app_back4.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}
.login-bg{
    position: absolute;
    height:100%;
    display: flex;align-items:center;justify-content: center;
    z-index:2;
    background-color: rgba(20,20,20,0.6);
    margin:0;
    width:100%;
}

.login{
    display:flex;align-items: center;flex-direction: row;
    box-shadow:2px 2px 15px rgb(0,0,0,0.7);
    z-index:4;
    width:70%;
    height:80%;
    overflow: hidden;
    border-radius:20px;
}
.login-signup{
    float: left;
    background-image: linear-gradient(to right,rgba(0,20,90),rgba(20,20,180));
    /* background:transparent; */
    width:51%;height:100%;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    overflow: hidden;
}
.caurosel{
    width:49%;
    height: 100%;
    z-index:-10;
}

.button{
    width:100%;height:8%;background: transparent;
    box-shadow: 0 0 25px rgba(0,0,0,0.7),0 0 25px rgba(0,0,0,0.7);
    z-index: 10;
}
.button button{
    font-family:Sniglet;
    text-shadow:0 0 3px rgb(20,20,20);
    width:50%;height: 100%;
    outline:none;border:none;font-size: 21px;font-weight: 600;
    transition: all 0.3s, background-position 0.9s ease;
    background: transparent;
    color:white;
    background-image:linear-gradient(45deg,transparent 0,transparent 50%,rgb(220,20,60) 50%,rgb(180,0,40) 100%);
    background-size:220%;cursor: pointer;
    box-shadow:0 0 8px rgba(0,0,0,0.7) inset;
}
#login{
    background-image:linear-gradient(45deg,transparent 0,transparent 50%,rgb(255,215,0) 50%,rgb(210,189,0) 100%);
}
.button button:active{
    font-size:14px;
}

.circle-temp1 {
    content: "";
    position: absolute;
    top: 63%;
    left: -203px;
    background-color:rgb(255,235,0);
    box-shadow:0 0 12px rgb(0,0,0) inset;
    border-radius: 50%;
    width: 750px;
    height: 750px;
    z-index:0;
    box-sizing:content-box;
}
.l .circle-temp1{
    background: rgb(180,255,230);
}
.circle-temp2 {
    content: "";
    position: absolute;
    top: 3%;
    right: -50px;
    background:rgb(20,61,75);
    box-shadow:0 0 12px rgb(0,0,0) inset;
    border-radius: 100%;
    width: 180px;
    height: 180px;
    box-sizing: content-box;
}
.circle-temp{
    content: "";
    position: absolute;
    top: -60%;
    left: -50%;
    background:rgb(10,40,140);
    box-shadow:0 0 22px rgb(0,0,0) inset;
    border-radius: 100%;
    width: 600px;
    height: 600px;
}
.circle-temp1,.circle-temp2,.circle-temp{
    transition:all 2s ease-in-out
}
.s .circle-temp{
    top:10%;
    left:5%;
    background-color:rgb(170,171,170);
    width:200px;height:200px;
}
.s .circle-temp2{
    top:-70%;
    left:220px;
    width: 600px;
    height: 600px;
    background-color:rgb(10,100,180)
}
.s .circle-temp1{
    background: rgb(185,251,250);
}
.lsmain{
    width:200%;height: 92%;display: flex;flex-direction: row;
    align-items: center;justify-content: center;
    transition: all 0.7s ease-in-out;
    background-image:linear-gradient(to right,rgba(240,240,240,0.9),rgba(250,250,250,0.9));
    box-shadow:0 0 33px rgba(0,0,0,0.5) inset;
}
.l,.s{
    width:100%;
    height:100%;   
    transform:scale(0.9);
    z-index:5;
    display:flex;align-items: center;flex-direction: column;
    padding-top:12%;
    border-radius: 40px;
    overflow: hidden;
    transition:ease-in-out all 0.4s;
    box-shadow:0 0 22px rgba(0,0,0) ,0 0 13px rgb(0,0,0) inset;
}
.s{
    background-color:rgb(15,20,50);
    padding-top: 8%;
}
.l{
    background-color:rgb(248,18,18);
}
.l .icon-surround{
    background-color: rgb(195,24,10);
}

.login-signup input{
    background: transparent;outline: none;border:none;font-family: inherit;
    color:white;font-size:16px;font-weight: 620;margin-left:0%;
    width:83%;height:100%;text-decoration: none;
    transition: all 0.4s ease-in-out;
}

.underline{
    margin-bottom:10%;padding-bottom:10px;width:75%;height:12%;
    display:flex;flex-direction: row;
    align-items: center;
    transition:all 0.6s ease-in-out;
    padding:8px;
    z-index:244;
    background-color: black;
    box-shadow:0 0 22px rgb(0,0,0),0 0 8px rgb(0,0,0) inset;
    border-radius:35px;
}
@keyframes anim3{
    0%{transform:   rotateZ( 0deg)}
    25%{transform:  rotateZ( 1deg)}
    75%{transform:  rotateZ(-1deg)}
    100%{transform: rotateZ( 0deg)}
}
.s .underline{
    margin-bottom:7%;
    height:11%;
    box-shadow: 0 0 10px rgba(0,0,0,0.7);
}
.icon{
    color:white;
    font-size:16px;
    text-shadow:0 0 5px rgb(250,250,250);
}
.l icon{
    color:white;
    font-size:13px;
}
.icon-surround{
    width:35px;height:35px;margin-right:2%;
    display:flex;justify-content: center;align-items: center;
    text-align:center;
    border-radius:50%;
    box-shadow: 0 0 8px rgba(20,20,0),0 0 5px rgb(0,0,0) inset;
    transition: ease-in-out all 0.6s;
}
.s .icon{
    font-size:13px;
}
.s .underline .icon-surround{
    background:rgb(15,80,140);   
}

input::placeholder{
    font-size:16px;font-weight: 600;color:rgba(250,250,250,0.8);
}

.submit{
    width:30%;
    height:10%;
    background-color:black;
    outline:none;border:none;font-size: 100%;
    font-weight: 600;display:flex;align-items: center;justify-content: center;
    transition: all 0.3s, background-position 0.9s ease;
    color:white;
    z-index:2789;
    font-family:Sniglet;
    text-shadow: 0 0 14px rgb(250,250,250);
    font-size:20px;
    cursor: pointer;
    box-shadow:0 0 8px rgba(0,0,0);
    border-radius:20px;
    transition:all 0.4s ease-in-out;
}
@keyframes anim4{
    0%{transform:translateX(0px)}
    25%{transform:translateX(3px)}
    75%{transform:translateX(-3px)}
    100%{transform:translateX(0px)}
}
.s .submit{
    background-color:black;    
    box-shadow:0 0 8px rgba(0,0,0,0.7);
}
.submit:active{
    transform:scale(0.9);   
}
.submit:hover{
    box-shadow: 0 0 23px rgba(0,0,0);
}

.tool-tip{
    visibility: hidden;
    transition: all 0.5s ease-in-out;
    position:absolute;
    z-index:10;
    background-color: rgba(20,20,20);
    padding:8px;
    left:75%;
    margin-top: 12%;
    display: flex;align-items: center;justify-content: center;
    box-shadow:0 0 22px rgba(0,0,0,0.4);
    border-radius:5px;
    font-size:12px;font-weight: 600;
    color:white;
    width:130px;
}
.icon1{
    display: none;
    color:rgb(10,140,10);
    font-size: 21px;
    text-shadow: 0 0 13px rgba(10,140,10,0.8);
}

.icon2{
    display: none;
    color:rgb(140,10,10);
    font-size: 21px;
    cursor:pointer;
    text-shadow: 0 0 13px rgba(140,10,10,0.8);
}
.icon3{
    color:rgba(140,10,10,0.9);
    font-size: 17px;
    margin-right:5px;
    cursor:pointer;
    text-shadow: 0 0 6px rgba(180,180,180,0.8);
}
.l .icon3{
    color:rgba(185,180,180);
}
.s .icon3{
    color:rgb(188,188,188)
}
.fa-eye-slash{
    display:none;
}
@keyframes anim1{
    0%{opacity:1;transform:scale(1);}50%{opacity:0;transform:scale(0.9);}100%{opacity:1;transform:scale(1);}
}

.temp{
    font-family:Pacifico;
    box-shadow:0 0 33px rgba(0,0,0,0.5) inset;
    width:calc(33.33% + 20px);
    height: 100%;
    z-index:-10;
    background-position: center;background-repeat: no-repeat;
    background-size: cover;background-clip: border-box;
    display:flex;align-items: center;justify-content: center;
    flex-direction: column;
}
.temp:nth-child(1){
    background-image: url('/static-img/first_image.jpg');
}
.temp:nth-child(2){
    background-image: url('/static-img/second_image.jpg');
}
.temp:nth-child(3){
    background-image: url('/static-img/third_image1.jpg');
}
.temp-cover{
    width:calc(100% - 10px);
    display:flex;align-items: center;justify-content: center;
    text-align: center;
    font-size: 52px;
    text-shadow:0 0 3px rgb(25,25,25);
    color:white;
    transition:ease-in-out 1.3s all;
    animation:floating infinite alternate 3s;
}
@keyframes floating {
    0% { transform: translate(0,  0px); }
    65% {transform:translate(0,15px)}
    100%   { transform: translate(-0px, -0px); }
}

.temp .temp-cover:nth-child(2){
    font-size:46px;
    color:rgb(190,190,190,0.9);
}
.caurosel-container{
    width:calc(300% + 60px);
    height: 100%;
    display:flex;flex-direction: row;
    transition:all 1.5s ease-in-out;
    /* animation:ease-in-out 15s infinite anim55; */
}
@keyframes anim55{
    0%{transform:translateX(0%);}
    20%{transform:translateX(0%);}
    33.33%{transform:translateX(-33.33%);}
    53.33%{transform:translateX(-33.33%);}
    66.66%{transform:translateX(-66.66%);}
    86.66%{transform:translateX(-66.66%);}
}
.changer{
    position: absolute;
    width:100px;
    z-index:10;
    height:30px;
    bottom:11.5%;
    left:30%;
    display:flex;align-items: center;justify-content: space-around;
}
.ind-changer{
    width:15px;
    height:15px;
    border-radius:50%;
    background-color: rgb(15,175,175);
    box-shadow:0 0 3px rgb(25,151,190) , 0 0 3px rgb(0,0,0) inset;
    transition:all 1.5s ease-in-out;
    cursor: pointer;
}

.ind-cover{
    border:4px rgb(25,151,190) solid;
    box-shadow:0 0 10px rgb(25,51,90) , 0 0 3px rgb(0,0,0) inset;
    width:33px;
    height:33px;
    border-radius: 50%;
    position: absolute;
    transform:scale(1.05);
    left:67px;
    transition:all ease-in-out 1.5s;
}


.loading-spinner {
    width: 70%;
    height: 70%;
    border: 4px solid #ffffff;
    text-shadow:0 0 3px rgb(255,255,255);
    border-top: 4px solid transparent;
    border-radius: 50%;
    animation:ease-in-out rotate infinite 1s;
  }
  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

.wrong-message{
    position: absolute;
    bottom:5%;
    left:-50%;
    transform:scale(0.5);
    height: 9%;
    padding-left:15px;
    padding-right:15px;
    transition:all 0.7s ease-in-out;
    display: flex;align-items: center;justify-content:space-around;
    color:rgba(255,255,255);
    border-radius:10px;
    background-color: rgb(250,20,60);
    z-index:10;
    box-shadow: 0 0 10px rgb(0,0,0) , 0 0 5px rgb(0,0,0) inset;
}
  