.vchat-holder{
  position:absolute;
  width:35%;
  height:95%;
  right:-35%;
  top:5px;
  /* z-index: 2; */
  background: transparent;
  transition:all 0.5s ease-in-out;
}
#chat-off{
  display:block;
}
#chat-on{
  display:none;
}
body {
    margin: 0;
    margin-top:0;
    width:100%;height:100%;
    overflow-x:hidden;
    /* background-image:linear-gradient(180deg,rgba(110,10,10,0.3),rgba(255,255,255,0.9)); */
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-user-select: none;  /* Chrome all / Safari all */
    -moz-user-select: none;     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  *,
  ::after,
  ::before {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  html {
    font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    /* padding:10px; */
    font-size: 16px;
    font-weight: normal;height:100%;
    line-height: 1.5;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    /* background: #fff; */
    color: rgb(20, 23, 26);
  }
  body {
    font-family: "Inter", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    margin-top:0;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: rgb(20, 23, 26);
  }
  
  .video-nav{
    height:60px;width:100%;margin-bottom:10px;
  }
  .video {
    width:100%;  
    z-index:0;
    /* height:70px; */
    background-color: black;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transition: all 0.3s ease;
    color:whitesmoke;
    border-radius: 15px;
    margin: 0 auto;
    /* margin-top: 2rem; */
    /* height:540px */
  }
  @media (min-width:150px){
    
    body{
      font-size:8px;
    }
    .row{visibility: hidden;}
    .video-slider{margin-bottom:-58px;};
    .video-slider-bg,.video-slider-rail,.video-slider-buffered{height:3px;}
  }
  @media (min-width: 700px) {
    
    body{
      font-size:13px;
    }
    .row{visibility: visible;}
    .video-slider{margin-bottom:-6px;}
    .video-slider-bg,.video-slider-rail,.video-slider-buffered{height:3px;}
  }
  @media (min-width: 1000px) {
    body{
      font-size:14px;font-weight: 600;
    }
  }
  
  
  .video:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    bottom: 0;
    left: 0;
    background: linear-gradient(#ffffff00, 85%, #000000c9);
    opacity: 1;
    transition: all 1s;
    -webkit-transition: all 1s;
  }
  .video-preview {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #000;
    z-index: 10;
    /* background-image: url(https://raw.githubusercontent.com/Ahmetaksungur/twitter-video-player-clone/master/92456705_506989316849451_7379405183454806542_n.jpg); */
    background-origin: border-box;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color:rgb(0,0,0,0.5);
    transition: all 0.3s ease;
  }
  :root {
    --primary: rgb(63,184,255);
  }
  .video-preview .video-start-btn {
    border: 4px solid rgb(25,25,25);
    background: rgb(25,25,25);
    box-shadow: 0 0 15px rgb(20,30,40), 0 0 10px rgb(0,0,0,0.6) ;
    border-radius: 9999px;
    display: flex;
    align-items: center;
    outline: none;
    user-select: none;
    cursor: pointer;
    width: fit-content;
    height: fit-content;
    transition: all 0.3s ease;
    position: relative;
    appearance: none;
  }
  .video-preview .video-start-btn svg {
    fill: var(--primary);
    overflow: hidden;
    opacity: 0.8;
  }
  .video-top {
    position: absolute;
    height: 90%;
    width: 100%;
    top: 0;
    right: 0;
    left: 0;
    background: transparent;
    z-index: 1;
  }
  .video-player-controls {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 10px;
  }
  .video-player-controls .row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: auto;
    position: relative;
  }
  .video-player-controls .wrap {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .video-control {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: fit-content;
    margin-right: 0.5rem;
    padding:1px;
    border-radius:50%;
    transition:all 0.3s ease;
  }
  .video-control:hover{
    background-color:rgb(220,20,60);
    transform:rotateZ(360deg);
  }
  .video-control .video-control-btn {
    border: none;
    background: transparent;
    color: white;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    position: relative;
    cursor: pointer;
    transition: all 350ms cubic-bezier(0.215, 0.61, 0.355, 1);
    appearance: none;
    outline: none;
    user-select: none;
  }
  .video-control .video-control-btn:hover {
    background: rgb(255 255 255 / 8%);
  }
  
  .video-control .video-control-btn .video-control-play {
    overflow: hidden;
    position: relative;
  }
  .video-control .video-control-btn .video-control-pause {
    overflow: hidden;
    position: relative;
    display: none;
  }
  .video-views {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: fit-content;
  }
  .video-counts {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: fit-content;
    margin-right: 0.5rem;
  }
  .video-counts .video-count-time {
  }
  .video-counts .video-count-line {
  }
  .video-counts .video-count-fulltime {
  }
  
  
  
  
  
  .video-pip{
    display:flex;align-items: center;justify-content: center;
    padding:6px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }
  .video-chat{
    margin-right: 10px;
    display:flex;align-items: center;justify-content: center;
    padding-top:3px;
    width:35px;height: 35px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.4s ease-in-out;
  }
  .video-chat:hover{
    transform:rotateZ(360deg);
    background-color: rgb(220 20 60); 
  }
  .video-pip:hover{
    transform:rotateZ(180deg);
    background-color: rgb(220 20 60);  
  }
  #canvas{display:none;}
  .video-pixel,.video-playback{
    margin-top:.3em;margin-bottom:.3em;
    padding:.3em;
    cursor:pointer;
    display:flex;align-items: center;justify-content: center;
    border-radius:5px;
  }
  .video-pixel:hover,.video-playback:hover{
    /* font-size:15px; */
    background-color: rgb(220,20,60);
    transition:all 0.2s ease;
  }
  .video-playback:hover{
    justify-content: flex-end;
  }
  .video-playback-rate,.video-pixel-rate{
    height:None;
    width:4em;
    z-index:2000;
    display:flex;align-items: center;justify-content: center;
  }
  .video-pixel-rate{
    margin-left:15px;
  }
  
  .video-playback-box,.video-pixel-box {
    transition: all 0.4s ease;
    font-size:13px;
    font-weight: 600;
    z-index:200;
    bottom:2.5em;
    width:6.5em;
    position: absolute;
    display:flex;align-items: center;justify-content: space-between;flex-direction: column;
    z-index: 9999;
    background:linear-gradient(rgba(0,0,0,0.2),rgba(220,20,60,0.4));
    background:rgba(0,0,0,0.2);
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2), 0 0 3px rgba(0, 0, 0, 0.2),
      0 0 0 3px rgba(255, 255, 255, 0.2);
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 9px;
    display:none;
    animation: moveit 800ms ease forwards;
    will-change: transform;
  }
  
  @keyframes moveit {
    10% { transform:  scale3d(0.96,0.96,1); }
    30% { transform: scale3d(1.1,1.1,1); }
    50% { transform:  scale3d(0.98,0.98,1); }
    70% { transform:  scale3d(1.05,1.05,1); }
    85% { transform: scale3d(1.01,1.01,1); }
    100% { transform:  scale3d(1,1,1); }
  }
  
  @keyframes moveitback {
    0% { transform: translateY(60px); }
    100% { transform: translateY(0) scale3d(0.96,0.96,1); }
  }
  
  
  .video-playback-speed,.video-pixel-speed{
    width:100%;
    display:flex;align-items: center;justify-content: center;
    margin-right: 1em;
    cursor:pointer;
    padding:5px;
    border-radius:30px;
  }
  .video-pixel-speed{
    padding:2px;padding-left: 17px;padding-right:17px;
    margin-left:30%;
    font-weight: 400;
  }
  .video-playback-speed:hover,.video-pixel-speed:hover{
    background: rgb(255 255 255 / 8%);
  }

  .video-voice {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: fit-content;
    margin-right: 1.5rem;
  }
  .video-voice .video-voice-btn {
    border: none;
    background: transparent;
    color: white;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    position: relative;
    cursor: pointer;
    transition: all 350ms cubic-bezier(0.215, 0.61, 0.355, 1);
    appearance: none;
    outline: none;
    user-select: none;
  }
  .video-voice  .video-voice-btn:hover{
    transform: rotate(180deg);
    background-color: rgb(220 20 60 );
    transition:all 0.4s ease;
  }
  .video-voice .video-voice-btn .video-voice-on {
    overflow: hidden;
    position: relative;
  }
  .video-voice .video-voice-btn .video-voice-off {
    overflow: hidden;
    position: relative;
    display: none;
  }
  .video-voice:hover .video-voice-slider {
    visibility: visible;
    opacity: 1;
  }
  .video-voice-slider {
    width: 120px;
    transition: all 0.4s ease;
    transform: rotate(-90deg);
    position: absolute;
    bottom: 80px;
    z-index: 9999;
    background: #0000002e;
    padding: 13px;
    border-radius: 9999px;
    visibility: hidden;
    opacity: 0;
  }
  .video-voice-slider-rail {
    width: fit-content;
    height: 6px;
    background: #b7b7b74d;
    border-radius: 9999px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
  }
  .video-voice-slider-rail:hover {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  }
  .video-voice-slider-buffer {
    height: 6px;
    background:rgb(220,20,60);
    border-radius: 9999px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 0;
  }
  
  .video-voice-slider-range {
    -webkit-appearance: none;
    width:100%;
    height: 100%;
    background: transparent;
    outline: none;
    margin: 0;
    padding: 0;
    z-index: 15;
  }
  
  .video-voice-slider-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    margin: 0;
    border: none;
    outline: none;
  }
  
  .video-voice-slider-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 15px;
    height: 15px;
    background: white;
    border-radius: 9999px;
    cursor: pointer;
    margin-top: -4px;
    align-self:center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  }
  
  .video-voice-slider-range::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    background: none;
    border-radius: 9999px;
  }
  
  .video-voice-slider-range:focus::-webkit-slider-runnable-track {
    background: none;
  }
  .video-voice-slider-range:active::-webkit-slider-runnable-track {
    background: none;
  }
  
  .video-voice-slider-range::-webkit-slider-thumb:active {
    box-shadow: 0 0 26px rgba(0, 0, 0, 0.2), 0 0 10px rgba(0, 0, 0, 4),
      0 0 0 5px rgba(255, 255, 255, 0.2);
  }
  .video-voice-slider-range::-webkit-slider-thumb:hover {
    box-shadow: 0 0 26px rgba(0, 0, 0, 0.2), 0 0 10px rgba(0, 0, 0, 4),
      0 0 0 5px rgba(255, 255, 255, 0.2);
  }
  
  .full-screen {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: fit-content;
  }
  
  .full-screen .full-screen-btn {
    border: none;
    background: transparent;
    color: white;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 9999px;
    position: relative;
    cursor: pointer;
    transition: all 350ms cubic-bezier(0.215, 0.61, 0.355, 1);
    appearance: none;
    outline: none;
    user-select: none;
  }
  .full-screen .full-screen-btn:hover {
    /* background: rgb(255 255 255 / 8%); */
    background-color:rgb(220,20,60);
    transform:rotateZ(360deg);
    transition:all 0.4s ease;
  }
  
  .full-screen .full-screen-btn .full-screen-open {
    overflow: hidden;
    position: relative;
  }
  .full-screen .full-screen-btn .full-screen-exit {
    overflow: hidden;
    position: relative;
    display: none;
  }
  .video .video-player {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    user-select: none;
  }
  .video .video-player video {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    object-fit: contain;
    object-position: center;
    user-select: none;
  }
  .row{transition:all 0.5s ease;}
  .video-slider {
    position: relative ;
    width: 99%;
    transition:all 0.5s ease;
  }
  .video-slider-container {
    width: 100%;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background: transparent;
    position: relative;
    cursor: pointer;
  }
  .video-slider-bg {
    width: 100%;
    background: #ffffff6b;
    height: 4px;
    border-radius: 9999px;
    position: relative;
    display: flex;
    align-items: center;
    transition: background 100ms cubic-bezier(0.6, -0.28, 0.74, 0.05);
  }
  .video-slider-bg:hover {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  }
  .video-slider-rail {
    width: 0%;
    background:rgb(220,20,60);
    height: 4px;
    border-radius: 9999px;
    transition:all 0.1s ease-in-out;
    position: relative;
  }
  .video-slider-buffered {
    width: 0%;
    background:rgba(215,215,215,0.8);
    transition:all 0.1s ease-in-out;
    height: 4px;
    border-radius: 9999px;
    position: absolute;
  }
  .video-slider-buffer {
    position: absolute;
    left: 0%;
    width: 15px;
    height: 15px;
    transition:all 0.1s ease-in-out;
    background:rgb(220,20,60);
    border-radius: 9999px;
    cursor: pointer;
    transition: transform 100ms cubic-bezier(0.6, -0.28, 0.74, 0.05);
    transform: scale(0);
    opacity: 0;
  }
  .video-slider-tooltip {
    position: absolute;
    left: 0%;
    display:flex;align-items: center;justify-content: center;
    bottom:100%;
    padding-left:7px;padding-right:7px;
    width:none;
    height: 20px;
    border-radius:3px;
    color:white;
    background:rgb(220,20,60);
    display:none;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.2), 0 0 2px rgba(0, 0, 0, 0.2),
      0 0 0 2px rgba(0, 0, 0, 0.2);
  }
  .video-slider-buffer:hover {
    box-shadow: 0 0 26px rgba(0, 0, 0, 0.2), 0 0 10px rgba(0, 0, 0, 4),
      0 0 0 5px rgba(255, 255, 255, 0.2);
  }
  .video-slider-container:hover .video-slider-buffer {
    transform: scale(1);
    opacity: 1;
  }
  .video-slider-container:hover .video-slider-bg {
    background: #ffffff82;
  }
  
  .video-loading-spinner {
    width: 55px;
    height: 55px;
    border: 5px solid #ffffff;
    border-top: 5px solid #9e9e9e;
    border-radius: 50%;
  }
  
  .video-loading-spinner {
    -webkit-transition-property: -webkit-transform;
    -webkit-transition-duration: 1.2s;
    -webkit-animation-name: rotate;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
  
    -moz-transition-property: -moz-transform;
    -moz-animation-name: rotate;
    -moz-animation-duration: 1.2s;
    -moz-animation-iteration-count: infinite;
    -moz-animation-timing-function: linear;
  
    transition-property: transform;
    animation-name: rotate;
    animation-duration: 1.2s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
  }
  
  @-webkit-keyframes rotate {
    from {
      -webkit-transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
    }
  }
  
  @-moz-keyframes rotate {
    from {
      -moz-transform: rotate(0deg);
    }
    to {
      -moz-transform: rotate(360deg);
    }
  }
  
  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .video-loading {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #00000061;
    z-index: 1;
    transition: all 0.3s ease;
  }
  
  .video-reset {
    position:absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #00000061;
    z-index: 1;
    transition: all 0.3s ease;
    display: none;
  }
  .video-reset-btn {
    border: none;
    width:13%;height:7%;  
    background: rgb(220,20,60);
    transition:all 0.55s ease-in-out;    
    border-radius: 9999px;
    padding: 10px 20px;
    color: black;
    font-weight: 700;
    font-size: 13px;
    outline: none;
    user-select: none;
    cursor: pointer;
    appearance: none;
    box-shadow: 0 0 20px rgba(220,20,60, 0.7), 0 0 20px rgba(220,20,60, 0.7);
  }
  .video-reset-btn:hover {
    background: rgb(220,20,60);
    transform:scale(1.06);
    box-shadow: 0 0 20px rgba(220,20,60, 0.9), 0 0 20px rgba(220,20,60, 0.9);
  }
  .video-reset-btn:active{
    transform:scale(0.8)
  }
  .anim{
    z-index: 2010101;position: absolute;
    background-color: rgba(30,30,30,0.3);
    width:40px;
    height:40px;
    top:calc(50% - 40px);
    left:calc(50% - 40px);
    display:flex;align-items: center;justify-content: center;
    border-radius:50%;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.7), 0 0 15px rgba(0, 0, 0, 0.7),
      0 0 0 0px rgba(0, 0, 0, 0.5);
    /* transition: transform .2s ease-out; */
     
    opacity:0;
  }
  .anim:hover{
    -webkit-transform:scale(1.5);
    -moz-transform:scale(1.5);
    -o-transform:scale(1.5);
    transform:scale(1.5);
    transform:scale(1.5);
  }
  .anime{
    animation:animate3 0.6s ease-in-out infinite;
  }
  @keyframes animate3{
    0%{
      transform:scale(2.0);
      opacity:0;
    }
    10%{
      transform:scale(0.9);
    }
    50%{
      opacity:1;
    }
    70%{
      opacity:0.8;
    }
    100%{
      transform:scale(2.0);
      opacity:0.3;
    }
  }
  
  .video-incoming{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0,0,0,0.85);
    z-index: 10;
    transition: all 0.4s ease;
    /* display:none; */
    visibility: hidden;
  }
  .grad-text{
    /* background-color: #03e9f4; */
    position: absolute;
    display:table;
    border-spacing: 5px;
    /* border-collapse:separate; */
  }
  
  .grad-text span{
    font-family: "Trebuchet MS", Helvetica, sans-serif;
    text-align: center;
    width:31px;height:31px;
    font-size:20px;
    background-color: rgb(220,20,60);
    border-radius: 20px;
