@import url(https://fonts.googleapis.com/css?family=Sniglet);


.video-container{
    width:100%;
    height:100%;
    position: absolute;
    top:0;left:0;
    padding:10px;
    padding-left:15.5px;
    overflow-y:scroll;
}
.single-video{
    position: relative;
    float:left;
    overflow:hidden;
    width:32.75%;
    min-height:380px;
    max-height: 400px;
    margin-bottom:5px;
    margin-right:5px;
    background-image: linear-gradient(45deg,rgb(255,255,255,0.95),rgb(200,200,200,0.9));
    box-shadow:0 0 5px rgb(0,0,0) ;
    border-radius:25px;
    padding:10px;
    padding-bottom:20px;
    cursor: pointer;
    display:flex;align-items:center;
    transition:all 0.1s ease-in-out;
    justify-content: space-between;
    flex-direction: column;
   
}
.single-video:hover{
    box-shadow:0 0 5px rgb(0,0,0) ,0 0 5px rgb(0,0,0) inset;
    transform:scale(1.005)
}

.single-video .circle-temp{
    background-color: rgb(15,30,61);
    top:-280px;
    left:-40px;
}
.single-video .circle-temp1{
    background-color:rgb(250,220,50);
    width:300px;height: 300px;
    top:350px;
    left:280px;
}
.single-video .circle-temp2{
    background-color: rgb(215,10,11);
    width:300px;height: 300px;
    top:270px;
    left:-160px;
}
.sin-vid-poster{
    width:100%;
    height:250px;
    border-radius:20px;
    overflow: hidden;
    z-index:10;
    box-shadow:0 0 12px rgb(0,0,0);
}
.video-caurosel{
    position: relative;
    width:74%;
    height:520px;
    margin-left:13%;
    overflow:hidden;
    border-radius: 30px;
    margin-bottom:15px;
    background-image:linear-gradient(rgb(15,30,61),rgb(10,30,50));
    box-shadow: 0 0 22px rgb(0,0,0), 0 0 12px rgb(0,0,0) inset;
}
.video-caurosel .circle-temp1{
    top:350px;
}
.video-caurosel .circle-temp{
    left:-140px;
    top:-380px;
}
.circle-temp3 {
    content: "";
    position: absolute;
    bottom: -32%;
    right: -13%;
    background:rgb(180,12,5);
    box-shadow:0 0 12px rgb(0,0,0) inset;
    border-radius: 100%;
    width: 350px;
    height: 350px;
    box-sizing: content-box;
    transition:all 2s ease-in-out;
}
.circle-temp4 {
    content: "";
    position: absolute;
    top: -8%;
    right: 1%;
    background:rgb(20,175,175);
    box-shadow:0 0 12px rgb(0,0,0) inset;
    border-radius: 100%;
    width: 250px;
    height: 250px;
    transition:all 2s ease-in-out;
    box-sizing: content-box;
}
.img-vid-caurosel{
    z-index:0;
    border-radius: 20px;
    filter:blur(28px);
    background-position: center;
    background-size: cover;
    position:absolute;
    top:0;left:0;
}
.indv-caurosel{
    position: relative;
    height:400px;
    z-index: 2;
    border-radius:30px;
    box-shadow:0 0 12px rgb(0,0,0), 0 0 15px rgb(0,0,0) inset;
    background-color: transparent;
    background-position: center;
    background-size: cover;
    transform:scale(0.9);
    transition: all 1.6s ease-in-out,width 0.1s ease-in-out;
    cursor:pointer;
    overflow: hidden;
}
.caurosel-name{
    position:absolute;
    width:auto;
    padding:15px;
    bottom:0%;
    right:0%;
    text-align:end;
    color:white;
    background-color:rgb(255,10,11);
    box-shadow:0 0 12px rgb(0,0,0), 0 0 10px rgb(0,0,0) inset;
    border-top-left-radius: 40px;
    text-shadow: 0 0 13px rgb(250,250,250);
    font-size:20px;
    font-family:Sniglet;
    opacity: 0;
    transition: all 0.2s ease-in-out;
}
.vid-caurosel-mover{
    transition: all 1.6s ease-in-out;
}
.next-caurosel,.prev-caurosel{
    width:30px;
    height: 30px;
    background: linear-gradient(45deg,rgb(255,255,255) 0,rgb(230,230,230) 50%,rgb(253,10,10) 50%,rgb(203,15,15) 100%);
    background-size:300%;
    box-shadow:0 0 12px rgb(0,0,0),0 0 5px rgb(0,0,0) inset;
    border-radius: 50%;
    position:absolute;
    top:calc(50% - 15px);
    display:flex;align-items: center;
    justify-content: center;
    font-size:16px;
    text-shadow: 0 0 4px rgb(0,0,0);
    cursor: pointer;
    transition:all 0.6s ease-in-out;
    color:rgb(20,20,30)
}
.prev-caurosel{
    left:1%;
    padding-right:3px;
}
.next-caurosel{
    right:1%;
    padding-left: 3px;
}
.prev-caurosel:hover,.next-caurosel:hover{
    color:rgba(255,255,255,0.9);
    text-shadow: 0 0 4px rgba(255,255,255,0.9);
    background-position: 100%;
}
.prev-caurosel:active,.next-caurosel:active{
    transform:scale(0.9);
}

.vvideo-name{
    z-index:10;
    padding:10px; 
    color:rgb(250,255,250);
    text-shadow: 0 0 9px rgb(20,20,20);
    align-self: center;
    font-family:Sniglet;
    font-size:16px;
}
.vvideo-created{
    position: absolute;
    right: 0.5%;
    color:rgb(0,0,0,0.6);
    font-size:12px
}
.vvideo-views{
    position: absolute;
    left:0.5%;
    font-size:12px;
    background-color:rgb(0,0,0);
    border-radius:6px;
    box-shadow: 0 0 6px rgb(0,0,0,0.5);
    padding:3px;
    color:white;
}
.img-prof2{
    width:35px;
    height:35px;
    border-radius:50%;
    box-shadow:0 0 15px rgba(0,0,0,0.5) inset,0 0 8px rgb(0,0,0);
    background-color:rgb(220,20,60);
    display:flex;text-transform: uppercase;align-items: center;justify-content: center;
    font-size: 17px;font-weight: 500;
    font-family:Lobster;
    margin-right:5px;
    text-shadow:0 0 12px rgb(0,0,0);
    color:rgba(255,255,255,0.9);
}
.vvideo-owner{
    display:flex;flex-direction: row;
    align-items: center;
    cursor:pointer;
    font-family:Lobster;
    color:rgb(0,0,0,0.7)
}
.vvideo-info-holder{
    width:100%;
    position: relative;
    display:flex;flex-direction: row;
    align-items: center;
    justify-content: center;   
}
.vvideo-holder{
    position: absolute;
    top:7.5%;
    left:5%;
    width:90%;
    height:80%;
    transition:all 0.6s ease-in-out;
}

.vv-back{
    position: absolute;
    top:20px;
    left:1%;
    width:24px;
    height: 24px;
    border-radius: 50%;
    box-shadow: 0 0 12px rgb(20, 23, 26),0 0 5px rgb(0,0,0) inset;
    cursor:pointer;
    background-color: rgb(160,20,60);
    z-index:24440;
    display:flex;align-items: center;justify-content: center;
}
.vv-back:active{
    transform:scale(0.9)
}
.vv-back .fa-times{
    color:rgb(20,20,40);
}

.vlinnercircle1,.vlinnercircle{
    width:40px;
    height:40px;
    background-color: rgb(225,10,70);
    box-shadow:0 0 8px rgb(0,0,0),0 0 12px rgb(0,0,0) inset;
    border-radius: 50%;
    z-index:20;
    cursor: pointer;
    outline: none;border: none;
}
.vlinnercircle1{
    background-color:rgb(10,130,50);
} 

.vlinnercircle::-webkit-file-upload-button{
    visibility:hidden;
}
.vlinnercircle::before{
    content:'';outline:none;border:none;
    -webkit-user-select: none;
}
.vlinnercircle1:active,.vlinnercircle:active{
    transform:scale(0.95);
}
.vlk-vid-upload,.vlk-vid-upload1{
    font-size:18px;
    position: absolute;
    color:rgba(255,255,255,0.9);
    text-shadow:0 0 4px rgb(0,0,0);
    top:17px;
    left:16px;
    z-index:28;
    cursor:pointer;
}
.vlk-vid-upload:active,.vlk-vid-upload1:active{
    transform:scale(0.95);
}
.vlholder,.vlholder1{
    position:absolute;
    top:3%;
    left:2%;
    width:auto;
    padding:5px;
    z-index:20;
    border-radius:33px;
    height:auto;
    background-color:rgb(240,240,240);
    box-shadow:0 0 6px rgb(0,0,0) inset,0 0 12px rgb(0,0,0);
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.vlholder1{
    top:12%;
}
.vlano-holder,.vlano-holder1{
    overflow:hidden;
    width:0px;
    height:24px;
    flex-wrap: nowrap;
    font-family:Sniglet;
    font-weight: 650;
    font-size:17px;
    transition:ease-in-out all 0.5s;
}
.url-container{
    width:100%;
    height: 100%;
    top:0;
    left:0;
    position:absolute;
    transition: ease-in-out all 0.6s;
    z-index:2000;
    background-color:rgb(0,0,0,0.8);
}


.vlrl{
    position: absolute;
    top:calc(50% - 175px);
    left: calc(50% - 295px);
    width:490px;
    height:350px;   
    z-index:5;
    display:flex;align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top:12%;
    border-radius: 200px;
    overflow: hidden;
    background-color:red;
    transition:ease-in-out all 0.6s;
    box-shadow:0 0 22px rgba(0,0,0) ,0 0 13px rgb(0,0,0) inset;
    transform:scale(0);
}
.vlrl .circle-temp{
    top:-200px;
    left:-330px;
}
.vlrl .circle-temp1{
    width:300px; height: 300px;
    top:70%;left:250px;
}
.vlrl .circle-temp2{
    width:400px;height: 400px;
    top:-190px;
    right:-220px;
}
.vlrl .crbtn{
    width:25%;
    height:37px;
    left:37.5%;
    font-size:20px;
}
.vlrl .join-room{
    font-size:13px;
}
.vlrl-wr-message{
    position: absolute;
    bottom:5%;
    left:-25%;
    padding-left:10px;
    padding-right:10px;
    height:8%;
    transition:all 0.7s ease-in-out;
    display: flex;align-items: center;justify-content:space-around;
    color:rgba(255,255,255);
    text-shadow: 0 0 3px rgb(255,255,255);
    border-radius:15px;
    transform:scale(0.5);
    box-shadow: 0 0 10px rgb(0,0,0);
    background-color: rgb(250,10,60);  
}
.vlrl-wr-message .fa-times-circle{
    font-size:20px;
    margin-right:5px;
    color:rgb(255,255,255,0.9);
    text-shadow: none;
}
.cr-loading-spinner {
    width: 23px;
    height: 23px;
    border: 3.5px solid #ffffff;
    border-top: 3.5px solid transparent;
    border-radius: 50%;
    animation:ease-in-out rotate infinite 1s;
}
@keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}
