.chat-window{
    width:100%;height: 100%;
    background-color: black;
    font-family:Sniglet;
    letter-spacing: 2.0;
}
::-webkit-scrollbar{
    width:5px;
    height:0%;
}
::-webkit-scrollbar-track{
    border-radius:10px;
}
::-webkit-scrollbar-thumb{
    background:rgba(20,20,20,0.9);
    border-radius:10px;
}
.video .ch-o-bt::-webkit-scrollbar{
    display:none;
}
#inp::-webkit-scrollbar-thumb{
    background: rgba(255,255,255,0.7);
}
.ch-o-bt::-webkit-scrollbar-thumb{
    background: rgba(25,25,55,0.5);
}

.chat-title{
    position:absolute;
    top:0;
    width:100%;
    height:6%;
    z-index:15;
    background-color: rgb(40,50,40);
    padding-left:10px;
    padding-right: 10px;
    color:white;
    font-family:Sniglet;
    font-size: 20px;
    display:flex;align-items: center;justify-content: center;padding-right:10px;
    box-shadow: 0 0 13px rgba(20,20,20, 0.8), 0 0 13px rgba(20,20,20, 0.8) inset;
    transition:all 0.6s ease-in-out;
}
.chat-title .fa-comment-alt{
    margin-left:8px;
}
.chat-title img{
    opacity:0.6;
}
.chat-output{
    width:100%;
    height:100%;
    background-image: url("/static-img/chat_img.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}
.ch-o-bt{
    padding-top:6.2%;
    position: absolute;
    bottom:3rem;
    width:100%;
    min-height: 0px;
    max-height: 90%;
    overflow: hidden;
    overflow-y: scroll;
    transition:all 0.4s ease-in-out;
}
.video .chat-window{
    background: transparent;
}
.video .chat-output{
    transition:0.4s all ease-in-out;
    position: absolute;background: transparent;
    bottom:0.5%;height: 100%;
}
.video .ch-o-bt {
    padding-top:0%;
    z-index:1;
    -ms-overflow-style: none;  
    scrollbar-width: none;  
    right:5px;
    max-height: 100%;
}
.message-time{
    position: relative;
    margin-left:92%;
    color:rgba(255,255,255, 0.7);
    font-size: 10px;
}
.message-owner{
    font-size:16px;
    margin-bottom:4px;
    font-weight: 700;
}
.video .message-other .message-owner{
    opacity: 0.8;
}
.message-mine{
    background-color: rgba(220,20,60,0.8);
    margin-left:20%;
    margin-bottom:2%;
    padding:8px;
    transform:translateX(50%);
    box-shadow: 0 0 12px rgba(0,0,0, 0.5), 0 0 12px rgba(0,0,0, 0.5) inset;
    border-radius:10px;
    border-top-right-radius: 2px;
    color:white;
    width:80%;
    z-index:10;
    min-height:30px;
    font-family:Sniglet;
    transition:transform 0.5s ease-in-out;
}
.video .message-mine{
    background-color:rgba(5,20,60,0.6);
    color:rgba(255,255,255,0.8);
}
.message-other{
    margin-left:2%;
    margin-bottom:2%;
    padding:8px;
    transform:translateX(-50%);
    background-color: rgb(35,35,35);
    box-shadow: 0 0 12px rgba(0,0,0, 0.5), 0 0 12px rgba(0,0,0, 0.5) inset;
    border-radius:10px;
    border-top-left-radius: 2px;
    color:white;
    width:80%;
    font-family:Sniglet;
    z-index:10;
    min-height:60px;
    transition:transform 0.5s ease-in-out;
}
.video .message-other{
    background-color:rgba(35,35,35,0.6);
    color:rgba(255,255,255,0.8);
}
.chat-input{
    position: absolute;
    bottom:0;
    width:100%;
    min-height:3.5rem;
    max-height:7.5rem;
    z-index:15; 
    display:flex;align-items: center;
    background-color: transparent;
    padding-left:10px;
}
.inp-surround{
    width:85%;
    outline:none;
    background-color:rgb(25,32,25);
    min-height:2.5rem;
    max-height: 6.8rem;
    padding:10px;
    border-radius:1.25rem;
    border:none;
    box-shadow: 0 0 12px rgba(0,0,0, 0.5), 0 0 12px rgba(0,0,0, 0.5) inset;
    transition: all 0.3s ease-in-out;
    overflow-wrap:break-word;
}
#inp{
    outline:none;
    min-height:1.1rem;
    max-height:6rem;
    width:98%;
    resize:none;
    border:none;
    background: transparent;
    overflow:hidden;
    overflow-y:scroll;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color:white;
    font-weight: 600;
    font-family: inherit;transition: all 0.4s ease-in-out;
}
#inp::placeholder{
    color:white;opacity:0.5;
}
.chat-send{
    background-image:linear-gradient(45deg,rgb(25,125,85) 0,rgb(20,120,80) 50%,rgb(3,172,172) 50%,rgb(25,180,180) 100%);
    background-size: 300%;
    width:37px;
    height:37px;
    transform:rotateZ(180deg);
    margin-left: 2%;
    cursor:pointer;
    border-radius:50%;display:flex;align-items: center;justify-content: center;
    box-shadow: 0 0 5px rgba(0,0,0, 0.8), 0 0 5px rgba(0,0,0, 0.8), 0 0 5px rgb(0,0,0) inset ;
    transition: all 0.6s ease-in-out;
}
.chat-send .fa-pen-nib{
    font-size:15px;
    color:white;
    text-shadow: 0 0 12px rgb(255,255,255);
}
.chat-send:hover{
    background-position: 100%;
}
.chat-loader-surround{ 
    position: absolute;
    top:0%;
    left:calc(50% - 15px);
    background-color:rgb(35,35,35);
    box-shadow: 0 0 23px rgb(0,0,0);
    width:40px;height: 40px;
    display: flex;align-items: center;justify-content: center;
    transition: ease-in-out all 1s;
    border-radius:50%
}
.chat-loader {
    width: 25px;
    height: 25px;
    border: 3.7px solid rgba(255,255,255,0.8);
    border-top: 3.7px solid transparent;
    animation:anim232 ease-in-out infinite 1s;
    border-radius: 50%;
}
@keyframes anim232{
    0%{transform:rotateZ(0deg);}
    100%{transform:rotateZ(360deg);}
}
@keyframes anim23{
    0%{top:0%}
    20%{top:7%;}
    80%{top:7%;}
    100%{top:0%}
} 
.chat-date{
    width:30%;
    margin-left:40%;
    margin-top:20px;
    margin-bottom: 10px;
    background-color: rgb(40,50,40);
    box-shadow:0 0 12px rgb(0,0,0);
    border-radius:7px;
    padding:5px;
    padding-left:10px;
    padding-right:10px;
    color:white;
    font-size:13px;
    height:35px;
    display:flex;justify-content: center;align-items: center;
}

