.progress-ring {
  
}
.holder{
  position:absolute;
  top:calc(65% - 59px);
  left:calc(50% - 59px);
  width:118px;
  border-radius:50%;
  height:118px;
  background:transparent;box-shadow:0 0 12px rgb(120,120,120,0.4);
}
.ano-holder{
  background-color:rgb(30,30,40,0.5);
  box-shadow:0 0 15px rgb(0,0,0) inset; 
  width:118px;
  border-radius:50%;
  height:118px;
  display: flex;
  align-items: center;justify-content: center;
}
.progress-ring__circle {
  stroke-linecap:round;
  z-index:2;
  stroke-width:12;
  transition: 0.5s stroke-dashoffset;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}

.innercircle{
  position: absolute;
  margin-bottom:1px;
  width:94px;
  height:94px;
  background-color: rgb(255,20,60);
  box-shadow:0 0 8px rgb(0,0,0),0 0 5px rgb(0,0,0) inset;
  border-radius: 50%;
  z-index:2;
  cursor: pointer;
  outline: none;border: none;
}

.innercircle::-webkit-file-upload-button{
  visibility:hidden;
}
.innercircle::before{
  content:'';outline:none;border:none;
  -webkit-user-select: none;
}
.innercircle:active{
  transform:scale(0.95);
}
.k-vid-upload:active{
  transform:scale(0.95);
}

.cloudCancel,.cloudSubmit{
  position:absolute;
  top:calc(65% - 25px);
  left:60%;
  width:50px;height: 50px;
  cursor: pointer;
  border-radius: 50%;
  background-image:linear-gradient(45deg,rgb(255,255,255) 0,rgb(200,200,200) 50%,rgb(20,220,200) 50%,rgb(10,210,210) 100%);
  transition: all 0.7s ease-in-out,transform 0.01s ease-in-out;
  background-size: 300%;
  box-shadow: 0 0 12px rgb(120,120,120,0.4),0 0 7px rgb(0,0,0) inset;
  display:flex;align-items: center;justify-content: center;
}
.cloudCancel:hover,.cloudSubmit:hover{
  background-position: 100%;
  box-shadow: 0 0 12px rgb(0,0,0),0 0 5px rgba(25,25,20,0.8) inset;
}
.cloudSubmit:hover .fa-arrow-right {
  color:rgb(255,255,255);
}
.cloudCancel:hover .cancelreq {
  color:rgb(255,255,255);
}
.cloudCancel,.cloudSubmit:active{
  transform:scale(0.9);
}

.cancelreq,.fa-arrow-right{
  transition: all 0.7s ease-in-out;
  color:rgb(20,20,0);
  font-size:20px; 
  font-weight: 600;
}
.fa-check,.k-vid-upload{
  position:absolute;
  color:white;font-size: 50px;
  z-index:10;
  /* top:calc(65%-25px); */
  text-shadow: 0 0 8px rgb(0,0,0);
  cursor: pointer;
}
.corr,.wro{
  margin-right:10px;
}
.wr-message,.message{
  position: absolute;
  bottom:5%;
  left:-25%;
  padding-left:10px;
  padding-right:10px;
  height:8%;
  transition:all 0.7s ease-in-out;
  display: flex;align-items: center;justify-content:space-around;
  color:rgba(255,255,255,0.8);
  border-radius:10px;
  background-color: rgb(50,200,60);
  box-shadow: 0 0 10px rgb(0,0,0);
}
.wr-message{
  background-color: rgb(250,50,20);
}

@keyframes anim111{
  0%{left:-25%;transform:scale(0.5)}
  10%{left:3%;transform:scale(1)}
  90%{left:3%;transform:scale(1)}
  100%{left:-25%;transform:scale(0.5)}
}
