.voice-slider {
    width: 100px;
    transition: width 0.5s ease-in-out;
    z-index: 9999;
    padding: 13px;
    border-radius: 9999px;
    
}
  .voice-slider-rail {
    width: fit-content;
    height: 4px;
    width:100%; 
    background: #b7b7b74d;
    border-radius: 9999px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
  }
  .voice-slider-buffer {
    height: hpx;
    background:rgb(3,180,255);
    border-radius: 9999px;
    position: absolute;
    top: 0; 
    right: 0;
    left: 0;
    bottom: 0;
    width: 0;
  }
  
  .voice-slider-range {
    -webkit-appearance: none;
    width: 100%;
    height: 100%;
    background: transparent;
    outline: none;
    margin: 0;
    padding: 0;
    z-index: 15;
    position: relative;
  }
  
  .voice-slider-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    margin: 0;
    border: none;
    outline: none;
  }
  
  .voice-slider-range::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 15px;
    height: 15px;
    background: white;
    border-radius: 9999px;
    cursor: pointer;
    margin-top: -5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  }
  
  .voice-slider-range::-webkit-slider-runnable-track {
    width: 100%;
    height: 4px;
    cursor: pointer;
    background: none;
    border-radius: 9999px;
  }
  
  .voice-slider-range:focus::-webkit-slider-runnable-track {
    background: none;
  }
  .voice-slider-range:active::-webkit-slider-runnable-track {
    background: none;
  }
  
  .voice-slider-range::-webkit-slider-thumb:active {
    box-shadow: 0 0 26px rgba(0, 0, 0, 0.2), 0 0 10px rgba(0, 0, 0, 4),
      0 0 0 5px rgba(255, 255, 255, 0.2);
  }
  .voice-slider-range::-webkit-slider-thumb:hover {
    box-shadow: 0 0 26px rgba(0, 0, 0, 0.2), 0 0 10px rgba(0, 0, 0, 4),
      0 0 0 5px rgba(255, 255, 255, 0.2);
  }

