.drp-dwn-box {
    position:absolute;
    transition: all 0.4s ease;
    font-size:13px;
    font-weight: 600;
    display:flex;justify-content: space-between;flex-direction: column;
    z-index: 9999;
    background:rgba(30,30,40,0.9);
    box-shadow:0 0 15px rgba(3,233,240,0.2),0 0 15px rgba(3,233,240,0.2),0 0 15px rgba(3,233,240,0.2);
    color:rgb(255,255,255,0.6);
    padding:18px;
    border-radius: 9px;
    -webkit-animation: moveit 800ms ease forwards;
            animation: moveit 800ms ease forwards;
    will-change: transform;
}

@-webkit-keyframes moveit {
    10% { transform:  scale3d(0.96,0.96,1); }
    30% { transform: scale3d(1.1,1.1,1); }
    50% { transform:  scale3d(0.98,0.98,1); }
    70% { transform:  scale3d(1.05,1.05,1); }
    85% { transform: scale3d(1.01,1.01,1); }
    100% { transform:  scale3d(1,1,1); }
}

@keyframes moveit {
    10% { transform:  scale3d(0.96,0.96,1); }
    30% { transform: scale3d(1.1,1.1,1); }
    50% { transform:  scale3d(0.98,0.98,1); }
    70% { transform:  scale3d(1.05,1.05,1); }
    85% { transform: scale3d(1.01,1.01,1); }
    100% { transform:  scale3d(1,1,1); }
}

